import React from "react";
import "./bookrow.css";
function Bookrow({
  books,
  setBooks,
  setBookid,
  setName,
  setAuthor,
  setEdit,
  setIndex
}) {
  // console.log(books);
  const bookDelete = (index) => {
    let cBooks = books;
    cBooks.splice(index, 1);
    setBooks([...cBooks]);
  };

  const bookEdit = (index) => {
    let book = books[index];
    // bookDelete(index);
    setBookid(book.bookid);
    setName(book.name);
    setAuthor(book.author);
    setIndex(index);
    setEdit(true);
  };

  return (
    <div className="tableContainer">
      <table>
        <th>Book Id</th>
        <th>Book Name</th> <th>Author</th>
        <th>Settings</th>
        {books.map((book, index) => {
          return (
            <>
              <tbody>
                <tr>
                  <td>{book.bookid}</td>
                  <td>{book.name}</td>
                  <td>{book.author}</td>
                  <td>
                    <button onClick={() => bookDelete(index)}>Delete</button>
                    <button onClick={() => bookEdit(index)}>Edit</button>
                  </td>
                </tr>
              </tbody>
            </>
          );
        })}
      </table>
    </div>
  );
}
export default Bookrow;
