import "./styles.css";
import { useState, useEffect, useRef } from "react";
import Bookrow from "./components/Bookrow";
export default function App() {
  const [bookid, setBookid] = useState("");
  const [author, setAuthor] = useState("");
  const [name, setName] = useState("");
  const [books, addBooks] = useState([]);
  const [edit, setEdit] = useState(false);
  const [editIndex, setEditIndex] = useState(null);
  const initialRender = useRef(true);
  useEffect(() => {
    const data = JSON.parse(localStorage.getItem("books"));
    if (data) {
      addBooks(data);
      console.log(data);
    }
  }, []);
  useEffect(() => {
    if (initialRender.current) {
      initialRender.current = false;
      return;
    }
    localStorage.setItem("books", JSON.stringify(books));
  }, [books]);
  const add_book = (e) => {
    // console.log("Invoked");
    e.preventDefault();
    const new_book = {
      bookid,
      name,
      author
    };
    if (edit) {
      let cb = books;
      books[editIndex] = new_book;
      addBooks([...cb]);
      setBookid("");
      setAuthor("");
      setName("");
      setEdit(false);
      return;
    }
    addBooks((currBooks) => [...currBooks, new_book]);
    setBookid("");
    setName("");
    setAuthor("");
    // console.log(books);
  };
  return (
    <div className="App">
      <h1>Library Record App</h1>
      <form onSubmit={add_book}>
        <div className="mainCont">
          <div className="labelContainer">
            <label>Book Id </label>
            <label>Book Name </label>
            <label>Author</label>
          </div>
          <div className="inputContainer">
            <input
              type="text"
              value={bookid}
              onChange={(e) => setBookid(e.target.value)}
              required
            />
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
            />
            <input
              type="text"
              value={author}
              onChange={(e) => setAuthor(e.target.value)}
              required
            />
          </div>
        </div>
        <button type="submit">{edit ? "Update" : "Add"} Book</button>
      </form>

      <Bookrow
        books={books}
        setBooks={addBooks}
        setBookid={setBookid}
        setName={setName}
        setAuthor={setAuthor}
        setEdit={setEdit}
        setIndex={setEditIndex}
      />
    </div>
  );
}
